import { observer } from "mobx-react";
import React, { Component, createRef } from "react";
import { elementSize } from "../utils";
import { Texture } from "./Texture";

export interface TextureProps {
    texture: Texture;
}

@observer
export default class TexturePreview extends Component<Partial<TextureProps>> {
    canvasRef = createRef<HTMLCanvasElement>();

    // todo: split into processing and render logic parts.
    // processing:
    // - take raw, and using the various settings create out.
    // - tile/stretch/both when needed to get to 1024 x 1024
    // - out is used as actual texture
    // rendering:
    // - take out, and render it in available area as preview.
    // - overlay tiling and cell previews
    componentDidUpdate = async () => {
        const canvas = this.canvasRef.current;
        if (!canvas || !this.props.texture?.out) return;
        const { mirror } = this.props.texture.settings;
        const availableSize = elementSize(canvas.parentElement!);
        const textureData = await createImageBitmap(
            this.props.texture.out.texture
        );

        const ctx = canvas.getContext("2d");
        if (!ctx) return;
        canvas.width = Math.min(textureData.width, availableSize.width);
        const scale = canvas.width / textureData.width;
        canvas.height = textureData.height * scale;
        ctx.scale(scale, scale);

        if (mirror) {
            const sw = textureData.width / 2,
                sh = textureData.height / 2;

            // render each quadrant in the opposite quadrant
            ctx.drawImage(textureData, sw, sh, sw, sh, 0, 0, sw, sw); // br -> tl
            ctx.drawImage(textureData, 0, sh, sw, sh, sw, 0, sw, sw); // bl -> tr
            ctx.drawImage(textureData, 0, 0, sw, sh, sw, sw, sw, sw); // tl -> br
            ctx.drawImage(textureData, sw, 0, sw, sh, 0, sw, sw, sw); // tr -> bl
        } else {
            // just render source in canvas
            ctx.drawImage(textureData, 0, 0);
        }

        // game preview / overlay
        if (this.props.texture.settings.grid) {
            const width = textureData.width / 16;
            const height = textureData.height / 16;
            ctx.setLineDash([5]);
            for (let x = 0; x < 16; x++) {
                // vertical lines
                ctx.moveTo(x * width, 0);
                ctx.lineTo(x * width, textureData.height);
            }
            for (let y = 0; y < 16; y++) {
                // horizontal lines
                ctx.moveTo(0, y * height);
                ctx.lineTo(textureData.width, y * height);
            }
            ctx.strokeStyle = "steelblue";
            ctx.stroke();
        }
    };

    render() {
        const { texture } = this.props;
        return (
            <>
                {texture && (
                    <canvas
                        ref={this.canvasRef}
                        data-rendering={texture.rendering}
                        data-revision={texture.settings.revision}
                    ></canvas>
                )}
                {!texture && <div className="no-content">No Texture</div>}
            </>
        );
    }
}
