import { isObject } from "lodash";
import set from "lodash/set";
import { Schema, ValidationError } from "yup";

const alphanumericRegex = /[^a-z0-9_]+/gi;
export const sanitizeString = (str: string): string => {
    return str.replace(alphanumericRegex, "");
};

// https://stackoverflow.com/a/56531945
export const elementSize = (element: HTMLElement) => {
    var computedStyle = getComputedStyle(element);

    let width = element.clientWidth; // width with padding
    let height = element.clientHeight; // height with padding

    height -=
        parseFloat(computedStyle.paddingTop) +
        parseFloat(computedStyle.paddingBottom);
    width -=
        parseFloat(computedStyle.paddingLeft) +
        parseFloat(computedStyle.paddingRight);
    return { height, width };
};

export type ValidationErrors<T> = {
    [K in keyof Partial<T>]: T[K] extends any[]
        ? ValidationErrors<T[K][number]>[]
        : string;
};

export function unwrapErrors<T>(
    object: T,
    schema: Schema<any, T>
): ValidationErrors<T> {
    try {
        schema.validateSync(object, { abortEarly: false });
    } catch (err) {
        if (err instanceof ValidationError) {
            const errors = {} as ValidationErrors<T>;
            for (const error of err.inner) {
                set(errors, error.path, error.message);
            }
            return errors;
        }
    }
    return {} as ValidationErrors<T>;
}

export function errorArray(errorObject: any): string[] {
    const msgs: string[] = [];
    if (!errorObject) return msgs;
    for (const key in errorObject) {
        if (Object.prototype.hasOwnProperty.call(errorObject, key)) {
            const el = errorObject[key];
            if (typeof el === "string") {
                msgs.push(el);
            }
            if (Array.isArray(el)) {
                el.forEach((e) => msgs.push(...errorArray(e)));
            }
            if (isObject(el)) {
                msgs.push(...errorArray(el));
            }
        }
    }

    return msgs;
}
