import { action, computed, observable } from "mobx";
import { observer } from "mobx-react";
import React, { Component } from "react";
import { sanitizeString, unwrapErrors } from "../utils";
import TextureComponent, { Texture, TextureSchema } from "./Texture";
import {
    simpleMode_stuffCost,
    simpleMode_stats,
    simpleMode_stuffCategories,
    simpleMode_researchPrerequisites,
    simpleMode_tags,
    simpleMode_burnedTerrain,
} from "../data/defaults";
import { about } from "../StuffedFloorModGenerator";
import * as yup from "yup";
import { FloorSettings } from "./FloorSettings";
import { FloorBasics } from "./FloorBasics";
import random from "lodash/random";

export interface FloorProps {
    floor: Floor;
}

export const DefSchema = yup
    .string()
    .label("def")
    .trim()
    .matches(
        /^[a-z]+[a-z0-9_\.]*$/i,
        "def must start with a letter, and may not contain spaces or special characters other than '_'"
    );
export const CostSchema = yup.object().shape({
    def: DefSchema.required(),
    value: yup.number().label("cost").required().moreThan(0),
});
export const StatSchema = yup.object().shape({
    def: DefSchema.required(),
    value: yup.number().label("stat").required(),
});

export const FloorSchema = yup.object().shape({
    defName: yup.string().required(),
    label: yup.string().required(),
    description: yup.string().required(),
    texture: TextureSchema.required("floor must have a texture"),
    researchPrerequisites: yup.array().of(DefSchema.required()),
    stuffCost: yup.number().integer().positive(),
    stuffCategories: yup.array().required().of(DefSchema.required()),
    costs: yup.array().of(CostSchema),
    stats: yup.array().of(StatSchema),
    affordances: yup.array().required().min(1).of(DefSchema.required()),
    tags: yup.array().of(yup.string()),
    burnedDef: DefSchema,
    terrainAffordanceNeeded: DefSchema,
});

export class Floor {
    static floorCount = 0;

    id: string;
    @computed get defName(): string {
        return sanitizeString(`StuffedFloor_${about.name}_${this.label}`);
    }
    @observable label: string = "";
    @observable description: string = "";
    @observable texture?: Texture;
    @observable edgeType: EdgeType = "Hard";
    @observable stuffCategories = observable.array<string>([]);
    @observable stuffcost: number = 0;
    @observable costs: defValue[] = [];
    @observable stats: defValue[] = [];
    @observable affordances = observable.array<string>([
        "Light",
        "Medium",
        "Heavy",
    ]);
    @observable tags = observable.array<string>(["Floor"]);
    @observable terrainAffordanceNeeded: Affordance = "Heavy";
    @observable researchPrerequisites = observable.array<string>([]);
    @observable burnedDef?: string;

    @action resetFloor = (material: Material, type: Type) => {
        this.costs = [];
        this.affordances.replace(["Light", "Medium", "Heavy"]);
        this.terrainAffordanceNeeded = "Heavy";
        this.edgeType = "Hard";
        this.stuffCategories.replace(simpleMode_stuffCategories[material]);
        this.researchPrerequisites.replace(
            simpleMode_researchPrerequisites[material][type]
        );
        this.burnedDef = simpleMode_burnedTerrain[material];
        this.stats = simpleMode_stats[material][type];
        this.stuffcost = simpleMode_stuffCost[type];
        this.tags.replace(simpleMode_tags[type]);
    };

    @computed get validationErrors() {
        return unwrapErrors<Floor>(this, FloorSchema);
    }
    constructor() {
        this.id = `floor-${Floor.floorCount++}`;
        this.resetFloor("Wood", "Pretty");
    }
}

export interface TextureLoaderProps {
    setTexture: (texture: Blob) => void;
}

@observer
export default class FloorComponent extends Component<FloorProps> {
    constructor(props: FloorProps) {
        super(props);
        // this.fetchKitten();
    }

    async fetchKitten() {
        try {
            const result = await fetch(
                `https://placekitten.com/${random(200, 500)}/${random(
                    200,
                    500
                )}`
            );
            const blob = await result.blob();
            this.props.floor.texture = new Texture(blob);
        } catch (err) {
            console.error(`Error fetching kitten`);
        }
    }

    render() {
        const floor = this.props.floor;
        return (
            <div className="floor">
                <div className="top columns">
                    <div className="basics column is-half">
                        <FloorBasics floor={floor}></FloorBasics>
                        <FloorSettings floor={floor}></FloorSettings>
                    </div>
                    <div className="texture column is-half">
                        <TextureComponent floor={floor}></TextureComponent>
                    </div>
                </div>
            </div>
        );
    }
}
