import classNames from "classnames";
import { action, computed } from "mobx";
import { observer } from "mobx-react";
import React, { Component } from "react";
import { DeleteX } from "../StuffedFloorModGenerator";
import { errorArray, ValidationErrors } from "../utils";
import { Error } from "./Error";

interface CostListProps {
    id: string;
    costs: defValue[];
    errors?: ValidationErrors<defValue>[];
}

@observer
export default class CostList extends Component<CostListProps> {
    static costCount = 0;

    @computed get errorArray(): string[] {
        const { errors } = this.props;
        const msgs: string[] = [];
        if (errors) {
            for (const error of errors) {
                msgs.push(...errorArray(error));
            }
        }
        return [...new Set(msgs)];
    }

    @action add = () => {
        this.props.costs.push({
            id: `cost-${this.props.id}-${CostList.costCount++}`,
            def: "",
            value: 0,
        });
    };

    @action delete = (id: id) => {
        const removing = this.props.costs.findIndex((cost) => cost.id === id);
        if (removing < 0) return;
        this.props.costs.splice(removing, 1);
    };

    render() {
        const { costs, errors } = this.props;

        return (
            <div className="field">
                <label className="label">CostList</label>
                <div className="costlist tags">
                    {costs.map((defCount, index) => (
                        <Cost
                            cost={defCount}
                            key={defCount.id}
                            onDelete={this.delete}
                            errors={errors?.[index]}
                        />
                    ))}
                    <button
                        className="button is-success is-small"
                        onClick={this.add}
                    >
                        Add Cost
                    </button>
                </div>
                {this.errorArray.map((msg, index) => (
                    <Error touched={true} error={msg} key={index} />
                ))}
            </div>
        );
    }
}

interface CostProps {
    cost: defValue;
    errors?: ValidationErrors<defValue>;
    onDelete: (id: id) => void;
}

@observer
export class Cost extends Component<CostProps> {
    render() {
        const { cost, onDelete, errors } = this.props;
        return (
            <div className="defCount tag is-info">
                <input
                    id={`defCount-count-${cost.id}`}
                    placeholder="count"
                    value={cost.value}
                    onChange={(e) =>
                        (cost.value = parseInt(e.target.value) || 0)
                    }
                    style={{
                        width: `calc( ${
                            cost.value.toString().length
                        }ch + 5px )`,
                    }}
                    className={classNames({
                        blank: cost.value === 0,
                        error: errors?.value,
                    })}
                />
                
                <input
                    id={`defCount-def-${cost.id}`}
                    placeholder="thingDef"
                    value={cost.def}
                    onChange={(e) => (cost.def = e.target.value)}
                    style={{
                        width: cost.def
                            ? `calc( ${cost.def.length}ch + 10px )`
                            : `calc( ${"thingDef".length}ch + 10px )`,
                    }}
                    className={classNames({
                        blank: !cost.def,
                        error: errors?.def,
                    })}
                />
                <div className="remove" onClick={() => onDelete(cost.id)}>
                    <DeleteX />
                </div>
            </div>
        );
    }
}
