import { action, computed, observable } from "mobx";
import { observer } from "mobx-react";
import React, { Component } from "react";
import { ValueType } from "react-select";
import CreatableSelect from "react-select/creatable";
import { Error } from "./Error";

interface StringWithDefaultProps {
    label: string;
    selected: string;
    options: string[];
    error?: string;
    id: string;
    noneOption?: boolean;
    updateSelected: (value: string) => void;
}

@observer
export class StringWithDefaults extends Component<StringWithDefaultProps> {
    @observable touched = false;

    @computed get selectedOption(): option | undefined {
        const { selected } = this.props;
        if (selected) {
            return {
                label: selected,
                value: selected,
            };
        }
        return undefined;
    }

    @computed get options(): option[] {
        const opts = this.props.options.map((o) => ({ value: o, label: o }));
        if (this.props.noneOption) opts.push({ value: "", label: "<None>" });
        return opts;
    }

    @action onChange = (value: ValueType<option>) => {
        this.touched = true;
        this.props.updateSelected((value as option).value);
    };

    render() {
        const { label, id, error } = this.props;
        return (
            <div className="field">
                <label className="label" htmlFor={id}>
                    {label}
                </label>
                <CreatableSelect
                    id={id}
                    onChange={this.onChange}
                    onBlur={() => (this.touched = true)}
                    options={this.options}
                    value={this.selectedOption}
                    className="react-select"
                    classNamePrefix=" "
                />
                <Error touched={this.touched} error={error} />
            </div>
        );
    }
}
