import { action, observable } from "mobx";
import { observer } from "mobx-react";
import React, { ChangeEvent, PureComponent } from "react";
import { TextureLoaderProps } from "./Floor";

@observer
export default class UploadButton extends PureComponent<TextureLoaderProps> {
    @observable target = "";
    @action loadTexture = async (event: ChangeEvent<HTMLInputElement>) => {
        if (event.target.files?.length) {
            const file = event.target.files[0];
            this.props.setTexture(file);
            this.target = file.name;
        }
    };

    render() {
        return (
            <div className="file is-fullwidth has-name is-right">
                <label className="file-label">
                    <input
                        className="file-input"
                        type="file"
                        name="texture"
                        id="texture"
                        accept="image/*"
                        onChange={this.loadTexture}
                    />
                    <span className="file-cta fetch-button">Upload</span>
                    <span className="file-name">{this.target}</span>
                </label>
            </div>
        );
    }
}
