import { action } from "mobx";
import { observer } from "mobx-react";
import React, { Component, createRef } from "react";
import { TextureLoaderProps } from "./Floor";

const CORS_PROXY = "https://cors-anywhere.herokuapp.com/";

@observer
export default class FetchButton extends Component<TextureLoaderProps> {
    inputRef = createRef<HTMLInputElement>();

    @action
    onSubmit = async () => {
        const input = this.inputRef.current;
        if (!input) return;

        try {
            const result = await fetch(CORS_PROXY + input.value);
            const blob = await result.blob();
            this.props.setTexture(blob);
        } catch (err) {
            console.error(`Error fetching ${input.value}: \n${err}`);
        }
    };

    render() {
        return (
            <div
                className="field has-addons is-fullwidth"
                style={{ marginTop: "1em" }}
            >
                <div className="control is-expanded">
                    <input
                        type="text"
                        className="input"
                        ref={this.inputRef}
                        name="url"
                        id="url"
                    />
                </div>
                <div className="control">
                    <button
                        className="button is-info fetch-button"
                        onClick={this.onSubmit}
                    >
                        Fetch
                    </button>
                </div>
            </div>
        );
    }
}
