import handlebars from "handlebars";
import raw from "raw.macro";
import { About } from "./components/About";
import jszip from "jszip";
import { FloorStore } from "./StuffedFloorModGenerator";
import { sanitizeString } from "./utils";

const aboutTemplate = handlebars.compile<About>(raw("./templates/About.xml"));
const floorDefTemplate = handlebars.compile<FloorStore>(
    raw("./templates/StuffedFloorDefs.xml")
);

function RenderAbout(about: About) {
    return aboutTemplate({
        ...about,
        validationErrors: about.validationErrors,
        packageId: about.packageId,
    });
}

function RenderFloors(floors: FloorStore) {
    return floorDefTemplate(floors);
}

export async function BuildMod(about: About, floors: FloorStore) {
    const zip = new jszip();
    const modFolder = sanitizeString(about.name);
    const aboutXml = RenderAbout(about);
    zip.file(`${modFolder}/About/About.xml`, aboutXml);

    const floorXml = RenderFloors(floors);
    zip.file(`${modFolder}/Defs/Floors.xml`, floorXml);

    for (const floor of floors.floors) {
        if (floor.texture?.out)
            zip.file(
                `${modFolder}/Textures/Floors/${floor.defName}.png`,
                floor.texture.out.texture
            );
    }

    return await zip.generateAsync({ type: "blob" });
}
