import { action, autorun, computed, observable } from "mobx";
import { observer } from "mobx-react";
import React, { Component } from "react";
import FetchButton from "./FetchButton";
import { FloorProps } from "./Floor";
import TexturePreview from "./TexturePreview";
import UploadButton from "./UploadButton";
import * as yup from "yup";
import "bulma-slider";
import { errorArray } from "../utils";
import { Error } from "./Error";
import { TextureSize } from "./TextureSize";
import { Switch } from "./Switch";
import debounce from "lodash/debounce";

export interface TextureSettings {
    contrast: number;
    brightness: number;
    mirror: boolean;
    desaturate: boolean;
    revision: number;
    tile: boolean;
    tilesX: number;
    tilesY: number;
    stretch: boolean;
    grid: boolean;
}

export const TextureSchema = yup.object().shape({
    raw: yup.mixed().required("floor must have a texture"),
    out: yup
        .object()
        .required()
        .shape({
            texture: yup.mixed().required(),
            size: yup.object().when("raw", {
                is: (raw) => !!raw,
                then: yup
                    .object()
                    .shape({ width: yup.number(), height: yup.number() })
                    .test(
                        "dims",
                        "floor textures should be 1024x1024 pixels",
                        (size) => size?.height === 1024 && size.width === 1024
                    )
                    .required(),
            }),
        }),
});

type settingUpdateFn = <
    K extends keyof TextureSettings,
    V extends TextureSettings[K]
>(
    setting: K,
    value: V
) => void;

type settingToggleFn = (setting: BooleanProperties<TextureSettings>) => void;
type Size = { width: number; height: number };

export class Texture {
    @observable raw: {
        texture: Blob;
        size: Size;
    };
    @observable out?: {
        texture: Blob;
        size: Size;
    };
    @observable settings: TextureSettings;
    @observable rendering: boolean = false;

    @action updateSetting: settingUpdateFn = (setting, value): void => {
        this.settings[setting] = value;
        this.settings.revision++;
    };

    @action toggleSetting: settingToggleFn = (setting): void => {
        this.settings[setting] = !this.settings[setting];
        this.settings.revision++;
    };

    @action async updateTexture(settings: TextureSettings, raw: Blob) {
        const canvas = this.renderCanvas;
        const {
            brightness,
            desaturate,
            contrast,
            tile,
            tilesX,
            tilesY,
            stretch,
        } = settings;
        if (!canvas || !raw) return;
        this.rendering = true;

        const rawData = await createImageBitmap(raw);

        let width, height;
        if (stretch) {
            width = 1024;
            height = 1024;
        } else if (tile) {
            width = tilesX * this.raw.size.width;
            height = tilesY * this.raw.size.height;
        } else {
            width = this.raw.size.width;
            height = this.raw.size.height;
        }

        canvas.width = width;
        canvas.height = height;

        const ctx = canvas.getContext("2d");
        if (!ctx) return;

        const filter = `contrast(${contrast / 100}) saturate(${
            desaturate ? 0 : 1
        }) brightness(${brightness / 100})`;
        ctx!.filter = filter;

        if (tile) {
            const dest = {
                // take account of possible stretching
                width: width / tilesX,
                height: height / tilesY,
            };
            for (let x = 0; x < tilesX; x++) {
                for (let y = 0; y < tilesY; y++) {
                    // draw image in each tile
                    ctx.drawImage(
                        rawData,
                        x * dest.width,
                        y * dest.height,
                        dest.width,
                        dest.height
                    );
                }
            }
        } else {
            ctx.drawImage(rawData, 0, 0, width, height);
        }

        // after rendering canvas, update out texture
        canvas.toBlob((blob) => {
            if (blob)
                this.out = {
                    texture: blob,
                    size: { width: canvas.width, height: canvas.height },
                };
            else {
                console.error("failed creating out texture");
            }
            this.rendering = false;
        });
    }

    setTiles() {
        this.settings.tilesX = Math.floor(1024 / this.raw.size.width);
        this.settings.tilesY = Math.floor(1024 / this.raw.size.height);
    }
    private setRawSize = async () => {
        const image = await createImageBitmap(this.raw.texture);
        this.raw.size = {
            width: image.width,
            height: image.height,
        };
        this.setTiles();
    };
    private renderCanvas: HTMLCanvasElement = document.createElement("canvas");
    private updateTextureDebounced = debounce(this.updateTexture, 500, {
        trailing: true,
        leading: true,
        maxWait: 500,
    });

    constructor(raw: Blob) {
        this.raw = {
            texture: raw,
            size: {
                width: 0,
                height: 0,
            },
        };
        this.settings = {
            contrast: 100,
            brightness: 100,
            mirror: false,
            desaturate: false,
            tile: false,
            tilesX: 1,
            tilesY: 1,
            stretch: false,
            revision: 0,
            grid: false,
        };
        this.setRawSize();
        autorun(
            () => {
                const { settings, raw } = this;

                // revisions are incremented by settings changes, track it here to trigger autoruns.
                /* eslint-disable-next-line @typescript-eslint/no-unused-vars */
                const revision = settings.revision;
                if (raw) this.updateTextureDebounced(settings, raw.texture);
            },
            {
                name: "updateTexture",
            }
        );
    }
}

@observer
export default class TextureComponent extends Component<FloorProps> {
    @action updateTexture = (texture: Blob) => {
        this.props.floor.texture = new Texture(texture);
    };

    @computed get errors(): string[] {
        // console.log(
        //     this.props.floor.validationErrors.texture,
        //     errorArray(this.props.floor.validationErrors.texture as any)
        // );
        return errorArray(this.props.floor.validationErrors.texture as any);
    }

    render() {
        const { floor } = this.props;
        const { texture } = floor;
        return (
            <>
                <label className="label">Texture</label>
                {!texture && (
                    <>
                        <UploadButton
                            setTexture={this.updateTexture}
                        ></UploadButton>
                        <FetchButton
                            setTexture={this.updateTexture}
                        ></FetchButton>
                    </>
                )}
                <TexturePreview texture={texture} />
                {this.errors &&
                    this.errors.map((error, index) => (
                        <Error touched={false} error={error} key={index} />
                    ))}
                {texture && <TextureSize texture={texture} />}
                {texture && (
                    <>
                        <div className="columns">
                            <div className="column">
                                <Switch
                                    id={`${floor.id}-mirror`}
                                    rounded={true}
                                    outlined={true}
                                    small={true}
                                    label="Check pattern tiling"
                                    checked={texture.settings.mirror}
                                    onChange={() =>
                                        texture.toggleSetting("mirror")
                                    }
                                />
                            </div>
                            <div className="column">
                                <Switch
                                    id={`${floor.id}-grid`}
                                    rounded={true}
                                    outlined={true}
                                    small={true}
                                    label="Show floor grid"
                                    checked={texture.settings.grid}
                                    onChange={() =>
                                        texture.toggleSetting("grid")
                                    }
                                />
                            </div>
                            <div className="column">
                                <Switch
                                    id={`${floor.id}-desaturate`}
                                    rounded={true}
                                    outlined={true}
                                    small={true}
                                    label="Desaturate"
                                    checked={texture.settings.desaturate}
                                    onChange={() =>
                                        texture.toggleSetting("desaturate")
                                    }
                                />
                            </div>
                        </div>
                        <div className="field">
                            <label className="label">
                                Contrast: {texture.settings.contrast}%
                            </label>
                            <input
                                className="slider is-fullwidth is-small is-primary"
                                step="1"
                                min="0"
                                max="200"
                                value={texture.settings.contrast}
                                onChange={(ev) =>
                                    texture.updateSetting(
                                        "contrast",
                                        parseInt(ev.target.value)
                                    )
                                }
                                type="range"
                            />
                        </div>
                        <div className="field">
                            <label className="label">
                                Brightness: {texture.settings.brightness}%
                            </label>
                            <input
                                className="slider is-fullwidth is-small is-primary"
                                step="1"
                                min="0"
                                max="200"
                                value={texture.settings.brightness}
                                onChange={(ev) =>
                                    texture.updateSetting(
                                        "brightness",
                                        parseInt(ev.target.value)
                                    )
                                }
                                type="range"
                            />
                        </div>
                    </>
                )}
            </>
        );
    }
}
