import { action } from "mobx";
import { observer } from "mobx-react";
import React, { Component } from "react";
import { DeleteX, FloorStore } from "../StuffedFloorModGenerator";
import { Floor } from "./Floor";

/* eslint-disable jsx-a11y/anchor-is-valid */
// bulma requires an 'a' element for tab tags,
// which can't have a link.

interface TabManagerProps {
    floors: FloorStore;
    onTabChange: (tab: string) => void;
    tab: string;
}
@observer
export default class TabManager extends Component<TabManagerProps> {
    @action addFloor = () => {
        const { floors, onTabChange } = this.props;
        const floor = new Floor();
        floors.addFloor(floor);
        onTabChange(floor.id);
    };

    render() {
        const { tab, floors, onTabChange } = this.props;

        return (
            <div className="tabs is-boxed">
                <ul>
                    <li className={tab === "about" ? "is-active" : ""}>
                        <a onClick={() => onTabChange("about")}>About</a>
                    </li>
                    {floors.floors.map((floor) => (
                        <li
                            key={floor.id}
                            className={tab === floor.id ? "is-active" : ""}
                        >
                            <a onClick={() => onTabChange(floor.id)}>
                                {floor.label || floor.id}
                                {tab === floor.id && (
                                    <DeleteX
                                        className="delete-x"
                                        onClick={() =>
                                            floors.removeFloor(floor.id)
                                        }
                                    />
                                )}
                            </a>
                        </li>
                    ))}
                    <li className="create-floor-button">
                        <a onClick={this.addFloor}>+ Add Floor</a>
                    </li>
                </ul>
            </div>
        );
    }
}
