import React, { Component } from "react";
import { saveAs } from "file-saver";
import { observer } from "mobx-react";
import { action, observable } from "mobx";
import { FloorStore, submitting } from "../StuffedFloorModGenerator";
import { About } from "./About";
import { BuildMod } from "../mod-builder";
import classNames from "classnames";
import isEmpty from "lodash/isEmpty";

interface DownloadButtonProps {
    floors: FloorStore;
    about: About;
}

@observer
export default class DownloadButton extends Component<DownloadButtonProps> {
    @observable building = false;

    @action
    download = async () => {
        const { floors, about } = this.props;
        if (
            floors.floors.some((floor) => !isEmpty(floor.validationErrors)) ||
            !isEmpty(about.validationErrors)
        ) {
            submitting.set(true);
            console.log({
                floors: floors.floors.map((floor) => floor.validationErrors),
                about: about.validationErrors,
                truthy: {
                    floors: floors.floors.some(
                        (floor) => !isEmpty(floor.validationErrors)
                    ),
                    about: !isEmpty(about.validationErrors),
                },
            });
            return;
        } else {
            this.building = true;
        }
        const archive = await BuildMod(about, floors);
        saveAs(archive, `${this.props.about.name}.zip`);
        this.building = false;
    };

    render() {
        return (
            <div id="download-frame">
                <button
                    className={classNames({
                        button: true,
                        "is-primary": true,
                        "is-loading": this.building,
                    })}
                    onClick={this.download}
                >
                    Download Mod
                </button>
            </div>
        );
    }
}
