import uniq from "lodash/uniq";
import { action, computed, IObservableArray, observable } from "mobx";
import { observer } from "mobx-react";
import React, { Component } from "react";
import {
    ActionMeta,
    OptionsType,
    OptionTypeBase,
    ValueType,
} from "react-select";
import CreatableSelect from "react-select/creatable";
import { Error } from "./Error";

interface StringListProps {
    label: string;
    selected: IObservableArray<string>;
    options: string[];
    errors?: string[];
    id: string;
}

@observer
export class StringList extends Component<StringListProps> {
    @computed get options() {
        return this.props.options.map((o) => ({ label: o, value: o }));
    }

    @observable touched = false;

    @action
    handleChange = (
        _value: ValueType<OptionTypeBase>,
        meta: ActionMeta<OptionTypeBase>
    ) => {
        const value = _value as OptionsType<OptionTypeBase>;
        const { selected } = this.props;
        this.touched = true;
        switch (meta.action) {
            case "clear":
                selected.clear();
                break;
            case "select-option":
                selected.push(meta.option!.value);
                break;
            case "create-option":
                // new option is not listed in meta,
                // add whatever we don't already have
                // should be one element, but it can't
                // hurt to be safe, right?...
                const newOptions = value.filter(
                    (v) => selected.indexOf(v.value) < 0
                );
                for (const option of newOptions) {
                    selected.push(option.value);
                }
                break;
            case "pop-value":
            case "remove-value":
                const index = selected.indexOf(meta.removedValue!.value);
                if (index >= 0) {
                    selected.splice(index, 1);
                }
                break;
        }
    };
    render() {
        const { label, selected, id, errors } = this.props;
        const msgs = Array.isArray(errors) ? errors : [errors];
        return (
            <div className="field">
                <label className="label" htmlFor={id}>
                    {label}
                </label>
                <CreatableSelect
                    id={id}
                    isMulti
                    onChange={this.handleChange}
                    onBlur={() => (this.touched = true)}
                    options={this.options}
                    value={selected.map((cat) => ({
                        label: cat,
                        value: cat,
                    }))}
                    className="react-select"
                    classNamePrefix=" "
                />
                {errors &&
                    uniq(msgs).map((error, index) => (
                        <Error
                            touched={this.touched}
                            error={error}
                            key={index}
                        />
                    ))}
            </div>
        );
    }
}
