import React, { Component } from "react";
import DownloadButton from "./components/DownloadButton";
import { observer } from "mobx-react";
import AboutComponent, { About } from "./components/About";
import { action, autorun, IObservableValue, observable } from "mobx";
import FloorComponent, { Floor } from "./components/Floor";
import "./styles/style.scss";
import TabManager from "./components/TabManager";

export class FloorStore {
    @observable floors = observable.array<Floor>([]);
    @action addFloor(floor: Floor): void {
        this.floors.push(floor);
    }
    @action removeFloor(id: string): void {
        const floor = this.floors.find((f) => f.id === id);
        console.log({ id, floor });
        if (floor) this.floors.remove(floor);
    }

    constructor() {
        this.floors.push(new Floor());
        autorun(() =>
            console.log(
                `Added floor, we now have ${this.floors.length} floors...`
            )
        );
    }
}

@observer
export class DeleteX extends Component<Partial<React.SVGProps<SVGSVGElement>>> {
    render() {
        return (
            <>
                <svg
                    height="14"
                    width="14"
                    viewBox="0 0 20 20"
                    aria-hidden="true"
                    focusable="false"
                    {...this.props}
                >
                    <path d="M14.348 14.849c-0.469 0.469-1.229 0.469-1.697 0l-2.651-3.030-2.651 3.029c-0.469 0.469-1.229 0.469-1.697 0-0.469-0.469-0.469-1.229 0-1.697l2.758-3.15-2.759-3.152c-0.469-0.469-0.469-1.228 0-1.697s1.228-0.469 1.697 0l2.652 3.031 2.651-3.031c0.469-0.469 1.228-0.469 1.697 0s0.469 1.229 0 1.697l-2.758 3.152 2.758 3.15c0.469 0.469 0.469 1.229 0 1.698z"></path>
                </svg>
            </>
        );
    }
}

export const about = new About();
export const floors = new FloorStore();
export const submitting = observable.box(false);

@observer
export default class StuffedFloorModGenerator extends Component {
    @observable about: About = about;
    @observable floors: FloorStore = floors;
    @observable tab: string = "about";
    @observable submitting: IObservableValue<boolean> = submitting;

    @action changeTab = (tab: string) => {
        console.log({ tab });
        this.tab = tab;
    };

    render() {
        const floor = this.floors.floors.find((f) => f.id === this.tab);
        return (
            <div id="main" className="container">
                <div className="box">
                    <TabManager
                        floors={this.floors}
                        tab={this.tab}
                        onTabChange={this.changeTab}
                    ></TabManager>
                    {this.tab === "about" && (
                        <AboutComponent about={this.about}></AboutComponent>
                    )}
                    {floor && <FloorComponent floor={floor}></FloorComponent>}
                </div>

                <DownloadButton
                    about={this.about}
                    floors={this.floors}
                ></DownloadButton>
            </div>
        );
    }
}
