import { action, observable } from "mobx";
import { observer } from "mobx-react";
import React, { Component } from "react";
import Select from "react-select";
import { simpleMode_materials, simpleMode_type } from "../data/defaults";
import {
    stuffCategories,
    researchDefs,
    burnedFloorDefs,
    affordances,
    tags,
} from "../data/defs";
import { edgeTypes } from "../data/enums";
import CostList from "./CostList";
import { FloorProps } from "./Floor";
import StatList from "./StatList";
import { StringList } from "./StringList";
import Input from "./Input";
import { StringWithDefaults } from "./StringWithDefaults";

@observer
export class FloorSettings extends Component<FloorProps> {
    @observable detailed: boolean = false;
    @observable simple: SimpleSettings = {
        material: "Wood",
        type: "Pretty",
    };

    @action toggleDetailed = () => {
        this.detailed = !this.detailed;

        if (!this.detailed)
            this.props.floor.resetFloor(this.simple.material, this.simple.type);
    };

    @action setMaterial = (material: Material) => {
        this.simple.material = material;
        this.props.floor.resetFloor(material, this.simple.type);
    };

    @action setType = (type: Type) => {
        this.simple.type = type;
        this.props.floor.resetFloor(this.simple.material, type);
    };

    render() {
        const { floor } = this.props;
        const settings = this.detailed ? (
            /* eslint-disable react/jsx-pascal-case */
            <FloorSettings_Detail floor={floor}></FloorSettings_Detail>
        ) : (
            <FloorSettings_Simple
                floor={floor}
                settings={this.simple}
                setMaterial={this.setMaterial}
                setType={this.setType}
            ></FloorSettings_Simple>
            /* eslint-enable react/jsx-pascal-case */
        );

        return (
            <div className="settings">
                <div className="field">
                    <input
                        id={`${floor.id}-settings-detailed`}
                        type="checkbox"
                        className="switch is-rounded is-outlined is-small"
                        checked={this.detailed}
                        onChange={this.toggleDetailed}
                    />
                    <label htmlFor={`${floor.id}-settings-detailed`}>
                        advanced mode
                    </label>
                </div>
                {settings}
            </div>
        );
    }
}

@observer
export class FloorSettings_Detail extends Component<FloorProps> {
    render() {
        const { floor } = this.props;
        const { validationErrors: errors } = floor;
        return (
            <div className="settings-detail">
                <StringList
                    label="Stuff Categories"
                    id={`${floor.id}-stuff-categories`}
                    options={stuffCategories}
                    selected={floor.stuffCategories}
                    errors={errors.stuffCategories}
                ></StringList>
                <Input
                    label="Stuff Cost"
                    value={floor.stuffcost}
                    id={`${floor.id}-stuff-cost`}
                    onChange={(e) =>
                        (floor.stuffcost = parseInt(e.target.value) || 0)
                    }
                    error={errors.stuffcost}
                />
                <CostList
                    {...floor}
                    errors={floor.validationErrors.costs}
                ></CostList>
                <StatList
                    {...floor}
                    errors={floor.validationErrors.stats}
                ></StatList>
                <StringList
                    label="Research Prerequisites"
                    id={`${floor.id}-research-prerequisites`}
                    options={researchDefs}
                    selected={floor.researchPrerequisites}
                    errors={errors.researchPrerequisites}
                ></StringList>
                <StringWithDefaults
                    label="Burned Def"
                    id={`${floor.id}-burned-def`}
                    selected={floor.burnedDef || ""}
                    options={burnedFloorDefs}
                    error={errors.burnedDef}
                    updateSelected={(value) => (floor.burnedDef = value)}
                />
                <div className="field">
                    <label htmlFor={`${floor.id}-edge-type`} className="label">
                        Edge Type
                    </label>
                    <Select
                        id={`${floor.id}-edge-type`}
                        defaultValue={{
                            label: floor.edgeType as string,
                            value: floor.edgeType as string,
                        }}
                        onChange={(_option) => {
                            const option = _option as option;
                            floor.edgeType = option.value as EdgeType;
                        }}
                        options={edgeTypes.map((edge) => ({
                            label: edge,
                            value: edge,
                        }))}
                        className="react-select"
                        classNamePrefix=" "
                    ></Select>
                </div>
                <StringWithDefaults
                    label="Affordance Needed"
                    id={`${floor.id}-affordance-needed`}
                    selected={floor.terrainAffordanceNeeded}
                    options={affordances}
                    updateSelected={(value) =>
                        (floor.terrainAffordanceNeeded = value as Affordance)
                    }
                    error={errors.terrainAffordanceNeeded}
                />
                <StringList
                    label="Affordances"
                    id={`${floor.id}-affordances`}
                    options={affordances}
                    selected={floor.affordances}
                    errors={errors.affordances}
                ></StringList>
                <StringList
                    label="Tags"
                    id={`${floor.id}-tags`}
                    options={tags}
                    selected={floor.tags}
                    errors={errors.tags}
                ></StringList>
            </div>
        );
    }
}

export interface SimpleSettings {
    material: Material;
    type: Type;
}

interface SimpleSettingsProps extends FloorProps {
    settings: SimpleSettings;
    setMaterial: (mat: Material) => void;
    setType: (type: Type) => void;
}

@observer
export class FloorSettings_Simple extends Component<SimpleSettingsProps> {
    render() {
        const { settings, floor, setMaterial, setType } = this.props;
        return (
            <div className="settings-simple">
                <div className="field">
                    <label
                        htmlFor={`${floor.id}-simple-material`}
                        className="label"
                    >
                        Material
                    </label>
                    <Select
                        id={`${floor.id}-simple-material`}
                        defaultValue={{
                            label: settings.material,
                            value: settings.material,
                        }}
                        onChange={(_option) => {
                            const option = _option as option;
                            setMaterial(option.value as Material);
                        }}
                        options={simpleMode_materials.map((mat) => ({
                            label: mat,
                            value: mat,
                        }))}
                        className="react-select"
                        classNamePrefix=" "
                    ></Select>
                </div>
                <div className="field">
                    <label
                        htmlFor={`${floor.id}-simple-type`}
                        className="label"
                    >
                        Beauty
                    </label>
                    <Select
                        id={`${floor.id}-simple-type`}
                        defaultValue={{
                            label: settings.type,
                            value: settings.type,
                        }}
                        onChange={(_option) => {
                            const option = _option as option;
                            setType(option.value as Type);
                        }}
                        options={simpleMode_type.map((type) => ({
                            label: type,
                            value: type,
                        }))}
                        className="react-select"
                        classNamePrefix=" "
                    ></Select>
                </div>
            </div>
        );
    }
}
