import { observer } from "mobx-react";
import React, { Component } from "react";
import { FloorProps } from "./Floor";
import Input from "./Input";

@observer
export class FloorBasics extends Component<FloorProps> {
    render() {
        const { floor } = this.props;
        const { validationErrors: errors } = floor;

        return (
            <>
                <Input
                    label="Label"
                    error={errors.label}
                    id={`${floor.id}-label`}
                    value={floor.label}
                    onChange={(e) => (floor.label = e.target.value)}
                />
                <Input
                    label="Description"
                    id={`${floor.id}-desc`}
                    value={floor.description}
                    onChange={(e) => (floor.description = e.target.value)}
                    error={errors.description}
                    element={<textarea className="textarea" />}
                />
            </>
        );
    }
}
