import React, { Component } from "react";
import { observer } from "mobx-react";
import { computed, observable } from "mobx";
import { sanitizeString, unwrapErrors } from "../utils";
import * as yup from "yup";
import Input from "./Input";

interface AboutProps {
    about: About;
}

export class About {
    @observable name: string = "";
    @observable description: string = "";
    @observable author: string = "";
    @observable url: string = "";

    @computed get packageId() {
        return sanitizeString(this.author) + "." + sanitizeString(this.name);
    }

    @computed get validationErrors() {
        return unwrapErrors<About>(this, AboutSchema);
    }
}

export const AboutSchema = yup.object({
    name: yup.string().required(),
    description: yup.string().required(),
    author: yup.string().required(),
    url: yup.string().url(),
    packageId: yup.string().required(),
});

@observer
export default class AboutComponent extends Component<AboutProps> {
    render() {
        const {
            name,
            description,
            author,
            url,
            validationErrors: errors,
        } = this.props.about;
        return (
            <div id="about">
                <Input
                    label="Mod Name"
                    id="mod.name"
                    value={name}
                    onChange={(e) => (this.props.about.name = e.target.value)}
                    error={errors.name}
                />
                <Input
                    label="Mod Description"
                    id="mod.desc"
                    value={description}
                    onChange={(e) =>
                        (this.props.about.description = e.target.value)
                    }
                    error={errors.description}
                    element={<textarea className="textarea" />}
                />
                <Input
                    label="Author"
                    id="mod.author"
                    value={author}
                    onChange={(e) => (this.props.about.author = e.target.value)}
                    error={errors.author}
                />
                <Input
                    label="Link"
                    id="mod.url"
                    value={url}
                    onChange={(e) => (this.props.about.url = e.target.value)}
                    error={errors.url}
                />
            </div>
        );
    }
}
