import classNames from "classnames";
import { action, computed, observable } from "mobx";
import { observer } from "mobx-react";
import React, { ChangeEvent, Component } from "react";
import { DeleteX } from "../StuffedFloorModGenerator";
import { errorArray, ValidationErrors } from "../utils";
import { Error } from "./Error";

interface StatListProps {
    id: string;
    stats: defValue[];
    errors?: ValidationErrors<defValue>[];
}

@observer
export default class StatList extends Component<StatListProps> {
    static statCount = 0;

    @computed get errorArray(): string[] {
        const { errors } = this.props;
        const msgs: string[] = [];
        if (errors) {
            for (const error of errors) {
                msgs.push(...errorArray(error));
            }
        }
        return [...new Set(msgs)];
    }

    @action add = () => {
        this.props.stats.push({
            id: `stat-${this.props.id}-${StatList.statCount++}`,
            def: "",
            value: 0,
        });
    };

    @action delete = (id: id) => {
        const removing = this.props.stats.findIndex((stat) => stat.id === id);
        if (removing < 0) return;
        this.props.stats.splice(removing, 1);
    };

    render() {
        const { stats, errors } = this.props;

        return (
            <div className="field">
                <label className="label">StatList</label>
                <div className="statlist tags">
                    {stats.map((stat, index) => (
                        <Stat
                            stat={stat}
                            key={stat.id}
                            onDelete={this.delete}
                            errors={errors?.[index]}
                        />
                    ))}
                    <button
                        className="button is-success is-small"
                        onClick={this.add}
                    >
                        Add Stat
                    </button>
                </div>
                {this.errorArray.map((msg, index) => (
                    <Error touched={true} error={msg} key={index} />
                ))}
            </div>
        );
    }
}

interface StatProps {
    stat: defValue;
    errors?: ValidationErrors<defValue>;
    onDelete: (id: id) => void;
}

interface StatState {
    valueTemp: string;
}

@observer
export class Stat extends Component<StatProps, StatState> {
    @observable valueTemp: string;

    @action updateValue = (ev: ChangeEvent<HTMLInputElement>): void => {
        this.valueTemp = ev.target.value;
    };

    @action applyValue = (): void => {
        this.props.stat.value =
            parseFloat(this.valueTemp.replace(/,/g, ".")) || 0;
        this.valueTemp = this.props.stat.value.toString();
    };

    constructor(props: StatProps) {
        super(props);
        this.valueTemp = props.stat.value.toString();
    }

    render() {
        const { stat, errors, onDelete } = this.props;
        return (
            <div className="defCount tag is-info">
                <input
                    id={`defCount-def-${stat.id}`}
                    placeholder="thingDef"
                    value={stat.def}
                    onChange={(e) => (stat.def = e.target.value)}
                    style={{
                        width: stat.def
                            ? `calc( ${stat.def.length}ch + 10px )`
                            : `calc( ${"thingDef".length}ch + 10px )`,
                    }}
                    className={classNames({
                        blank: !stat.def,
                        error: errors?.def,
                    })}
                />
                =
                <input
                    id={`defCount-count-${stat.id}`}
                    value={this.valueTemp}
                    onChange={this.updateValue}
                    onBlur={this.applyValue}
                    style={{
                        width: `calc( ${
                            this.valueTemp.toString().length
                        }ch + 5px )`,
                    }}
                    className={classNames({
                        blank: stat.value === 0,
                        error: errors?.value,
                    })}
                />
                <div className="remove" onClick={() => onDelete(stat.id)}>
                    <DeleteX />
                </div>
            </div>
        );
    }
}
